import confetti from 'canvas-confetti';

function Success() {
    function firework() {
        var duration = 15 * 100;
        var animationEnd = Date.now() + duration;
        var defaults = { startVelocity: 25, spread: 360, ticks: 50, zIndex: 0 };
        //  startVelocity: , spread: , ticks: 
      
        function randomInRange(min, max) {
          return Math.random() * (max - min) + min;
        }
      
        var interval = setInterval(function () {
          var timeLeft = animationEnd - Date.now();
      
          if (timeLeft <= 0) {
            return clearInterval(interval);
          }
      
          var particleCount = 50 * (timeLeft / duration);
          // since particles fall down, start a bit higher than random
          confetti(
            Object.assign({}, defaults, {
              particleCount,
              origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 }
            })
          );
          confetti(
            Object.assign({}, defaults, {
              particleCount,
              origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 }
            })
          );
        }, 250);
      }
    
      firework();

    return(
        <>
        <script src="https://cdn.jsdelivr.net/npm/canvas-confetti@1.5.1/dist/confetti.browser.min.js"></script>

        <div class="flex flex-col w-screen h-svh items-center justify-center gap-8 overflow-hidden bg-yellow-200">
            <div class="flex flex-col w-[95%] h-[95%] items-center mx-8 rounded-sm bg-white border border-gray-400">
                <img src='images/.jpg' alt='' class="h-[50%]"></img>
                <div class="text-[30px] font-pop ">  !</div>

                <div class="flex flex-col gap-16 self-center mt-12 text-[20px] font-noto_sans"><div class="self-center font-Freesentation">   </div>
                <div>1.      QR    . <br></br>&nbsp;&nbsp;&nbsp;&nbsp;  QR      ,    .</div>
                <div>2.      ,       <br></br>&nbsp;&nbsp;&nbsp;&nbsp;lazyguineapig1@gamil.com .</div>
                <div>3.          .</div>
                </div>
            </div>
        </div>
        </>
    )
};

export default Success;

